

html,
body {
  padding: 0;
  margin: 0 auto;
  font-family: Consolas;/*, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
    Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;*/
  max-width:1000px;
}

a {
  color: inherit;
  text-decoration: none;
}

@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Consolas; /*'Montserrat', sans-serif;*/
  color: #fff;
}

.userControls {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 1000px;
    padding: 25px;
}




body {
  background-color: #101012;
}

.coin_app {
  display: flex;
  max-width: 1000px;
  flex-direction:row ;
  align-items: center;
  margin-top: 10px;
  color: #fff;
}

.header {
  display: flex;
  justify-content: center;
}
.coin_logo {
  width: 200px;
  padding: 20px;
}

@media (max-width: 900px) {
    
    .userControls {
        width: 280px;
        margin: 25px,0,25px,0;
        display: inline-block;
        padding: 0;
    }

    body{
        max-width:320px;
    }

}
